import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/layout';
import '../styles/javadoc.css';
import { Box } from '@chakra-ui/react';

const JavadocPage = ({ data, pageContext }) => {
  console.log(pageContext);
  const generated = data.allHtmlContent.edges[0].node.content
    .replaceAll(/(\.\.\/)+/g, '/')
    .replaceAll('.html', '')
    .replaceAll(
      /href="\/?(?!#)/g,
      `href="/${pageContext.currentRepo.name}/${pageContext.currentVersion.name}/javadoc/`,
    );

  return (
    <Layout>
      <Box
        w="100%"
        position="relative"
        dangerouslySetInnerHTML={{ __html: generated }}
      />
    </Layout>
  );
};

export const query = graphql`
  query ($name: String!) {
    allHtmlContent(filter: { name: { eq: $name } }) {
      edges {
        node {
          name
          content
        }
      }
    }
  }
`;

export default JavadocPage;
